	visibility: hidden;
	transition: opacity 0.2s ease-out;
	opacity: 0;
	a {
		width: 80px;
		height: 80px;
		background: $url_0 no-repeat center center $color_2;
		text-align: center;
		transition: .5s;
		display: block;
		&:hover {
			color: $color_5;
			background: $url_6 center 15px no-repeat $color_2;
		}
