.features {
	padding: 60px 0;
	.middle-sub-title {
		color: $color_19;
		font-size: 16px;
		font-family: $font_0, $font_1;
		font-weight: 300;
	}
	.middle-title {
		margin-top: 2em;
		font-weight: 300;
		color: $color_20;
		font-size: 40px;
		span {
			font-weight: 700;
		}
	}
	.nav-tabs {
		border-bottom: 0;
		text-align: center;
		margin-top: 50px;
		li {
			margin-bottom: 14px;
			width: 320px;
			height: auto;
			float: none;
			display: inline-flex;
			text-align: left;
			&.active {
				a {
					background: $color_6;
					color: $color_5 !important;
				}
				a::after {
					border-left-color: $color_6;
				}
			}
			a {
				//Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
				transition: all .3s ease;
				position: relative;
				background: $color_21;
				width: 100%;
				padding: 10px;
				//Instead of the line below you could use @include border-radius($radius, $vertical-radius)
				border-radius: 0;
				color: $color_22 !important;
				border: 1px solid $color_23;
				&:hover {
					color: $color_5 !important;
					//Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
					transition: all .3s ease;
					background: $color_6;
					color: $color_5;
				}
			}
			a::after {
				content: "";
				border-bottom: 20px solid transparent;
				border-left: 20px solid $color_21;
				border-left-color: $color_24;
				border-top: 20px solid transparent;
				height: 0;
				width: 0;
				position: absolute;
				left: auto;
				right: -20px;
				bottom: 4.5em;
				margin: 0 auto;
			}
		}
	}
	.title {
		font-size: 18px;
		margin-bottom: 0;
	}
	.title2 {
		font-size: 16px;
		display: block;
		margin-bottom: 10px;
	}
	.paragraph {
		font-size: 13px;
	}
	.tab-content {
		margin-top: 4em;
	}
	hr {
		max-width: 15%;
		border: 1px solid $color_6;
		background-color: $color_6;
		height: 1px;
	}
	li.active span {
		color: $color_5;
	}
}
.slider {
	.item {
		padding: 0;
	}
	.carousel-control-prev {
		position: absolute;
		bottom: auto;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 63px;
		color: $color_5;
		text-align: center;
		opacity: 1;
		height: 63px;
		top: 50%;
		//Instead of the line below you could use @include transform($scale, $rotate, $transx, $transy, $skewx, $skewy, $originx, $originy)
		transform: translateY(-50%);
		background: $color_2;
	}
	.carousel-control-next {
		position: absolute;
		bottom: auto;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 63px;
		color: $color_5;
		text-align: center;
		opacity: 1;
		height: 63px;
		top: 50%;
		//Instead of the line below you could use @include transform($scale, $rotate, $transx, $transy, $skewx, $skewy, $originx, $originy)
		transform: translateY(-50%);
		background: $color_2;
	}
	.carousel-control-prev-icon::before {
		content: "\keyboard_arrow_left";
		font-size: 42px;
		font-family: $font_2;
		line-height: 1.5;
	}
	.carousel-control-next-icon::before {
		content: "\keyboard_arrow_right";
		font-size: 42px;
		font-family: $font_2;
		line-height: 1.5;
	}
	.carousel-control-prev-icon {
		background: 0 0;
		width: 100%;
		height: 100%;
	}
	.carousel-control-next-icon {
		background: 0 0;
		width: 100%;
		height: 100%;
	}
	.bg-carousel {
		background: $url_5;
		height: auto;
	}
	.separator {
		padding-top: 40px;
		padding-bottom: 40px;
	}
	.slider-title h2 {
		color: $color_19;
		font-size: 25px;
		font-weight: 600;
		font-family: $font_4, $font_5;
		line-height: 32px;
		padding-bottom: 28px;
	}
	.slider-content p {
		font-family: $font_6, $font_7;
		font-weight: 300;
		font-size: 16px;
		color: $color_19;
	}
}
.item {
	width: 100%;
	height: 393px;
	padding: 35px 25px;
}
.item-1 {
	background-image: $url_1;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: right;
}
.none-list {
	list-style: none;
	padding: 0;
}
.features-list {
	margin-top: 30px;
	li {
		margin-bottom: 20px;
		display: flex;
		a {
			display: flex;
			align-items: center;
			height: 55px;
			padding: 5px 25px 7px 30px;
			font-size: 18px;
			color: $color_5;
			z-index: 5;
			font-weight: 400;
			background: 0 0;
			position: relative;
			//Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
			transition: color .35s ease;
			&:before {
				opacity: .3;
				position: absolute;
				content: '';
				top: 0;
				left: 0;
				width: 15px;
				height: 100%;
				background-color: $color_6;
				//Instead of the line below you could use @include transform-origin($origin-x, $origin-y, $origin-z, $only3d)
				transform-origin: left;
				//Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
				transition: width .35s ease;
				z-index: -1;
			}
			&:hover:before {
				width: 100%;
				opacity: 1;
				//Instead of the line below you could use @include transform($scale, $rotate, $transx, $transy, $skewx, $skewy, $originx, $originy)
				transform: scaleX(1);
			}
		}
	}
}
